#inpu>input {
  text-align: left;
}
body{
  margin: 0;
  border: 0;
}

.td{
  border-radius: 15px;
}
.tdblank{
  border-radius: 15px;
}
#overlay{
  position: absolute;
  width: 100vw;
  height: 100vh;
  top:0;
  left: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
  cursor: pointer;
  text-align: center;
  border-radius: 15px;
  transform: translate3d(0,0,1);
}

#overlaytext{
  border: 1px solid black;
  height: 30vh;
  align-content: center;
  width: 30vw;
  font-size: 40px;
  left: 35vw;
  top: 35vh;
  position: relative;
  background: cornflowerblue;
  text-align: center;
  vertical-align: middle;
  line-height: 90px;  
  border-radius: 15px;
}

#overlaytextbutton{
  border: 1px solid black;
  height: 50px;
  border-radius: 3px;
}

