.float-button {
  position: fixed;
  width: fit-content;
  height: fit-content;
  top: 5px;
  right: 5px;
  border: 1px solid black;
  padding: 5px;
  border-radius: 10px;
  transition: all 0.2s ease-in 0s;
  z-index: 9999;
  cursor: pointer;
}